import { isString, ShapeFlags, isObject, isFunction, isArray } from '@vue/shared';

/**
 * @description: ‰ΩúÁî®ÂíåhÂáΩÊï∞‰∏ÄÊ†∑ÔºåÂàõÂª∫vnode
 * @param {*} type Á±ªÂûã
 * @param {*} props Â±ûÊÄß
 * @param {*} childen ÊèíÊßΩ
 * @return { vnode } vnode
 */
// h('div',{},[])
export function creatVnode(
  type,
  props,
  children: unknown = null,
  patchFlag: number = 0,
  dynamicProps: string[] | null = null,
  isBlockNode = false
) {
  // Âå∫ÂàÜÁªÑ‰ª∂ÁöÑËôöÊãüdomËøòÊòØÊôÆÈÄöÂÖÉÁ¥†
  const shapeFlag = isString(type)
    ? ShapeFlags.ELEMENT
    : isObject(type)
    ? ShapeFlags.STATEFUL_COMPONENT
    : isFunction(type)
    ? ShapeFlags.FUNCTIONAL_COMPONENT
    : 0;
  const vnode = {
    _v_isVnode: true,
    type,
    props,
    children,
    key: props && props.key, // diffÁÆóÊ≥ï
    el: null, // ÂíåÁúüÂÆûÂÖÉÁ¥†ÂíådomÂØπÂ∫î
    component: null,
    shapeFlag // Âà§Êñ≠Á±ªÂûã
  };
  // children
  normalizeChildren(vnode, children);
  return vnode;
}

export function normalizeChildren(vnode, children) {
  let type = 0;
  if (children == null) {
    return;
  } else if (isArray(children)) {
    type = ShapeFlags.ARRAY_CHILDREN;
  } else {
    type = ShapeFlags.TEXT_CHILDREN;
  }
  vnode.shapeFlag = vnode.shapeFlag | type;
}

// Âà§Êñ≠ÊòØÂê¶ÊòØvnode
export function isVnode(vnode) {
  return vnode._v_isVnode;
}
export const TEXT = Symbol('text');
// ÂÖÉÁ¥†ÁöÑchildren ÂèòÊàêvnode
export function CVnode(child) {
  // ['text'],[h()]
  if (isObject(child)) {
    return child;
  }
  return creatVnode(TEXT, null, String(child));
}
