import { isFunction, isObject, ShapeFlags } from '@vue/shared';
import { componentPublicInstance } from './componentPublicInstance';
// ÂàõÂª∫ÁªÑ‰ª∂ÂÆû‰æã
export const createComponentInstance = (vnode) => {
  const instance = {
    vnode,
    type: vnode.type, // ÁªÑ‰ª∂ÁöÑÁ±ªÂûã,Áî®Êà∑Ëá™Â∑±ÂÜôÁöÑ
    props: {},
    attrs: {},
    data: { a: 1 },
    render: false,
    setupState: {}, // setupÂÖ•Âè£ÂáΩÊï∞ÁöÑÁä∂ÊÄÅ
    ctx: {}, // ‰ª£ÁêÜ
    proxy: {},
    isMounted: false // ÊòØÂê¶ÊåÇËΩΩ
  };
  instance.ctx = {
    _: instance
  };
  return instance;
};

// Ëß£ÊûêÊï∞ÊçÆÂà∞ÁªÑ‰ª∂ÂÆû‰æã
export const setupComponent = (instance) => {
  // ËÆæÁΩÆÂÄº
  const { props, children } = instance.vnode;
  instance.props = props;
  instance.children = children;
  let isStateFul = instance.vnode.shapeFlag && ShapeFlags.STATEFUL_COMPONENT;
  if (isStateFul) {
    // ÊúâÁöÑËØùÂ∞±ÊòØÊúâÁä∂ÊÄÅÁöÑÁªÑ‰ª∂
    setupStateComponent(instance);
  }
};
// ÊâßË°åsetup
function setupStateComponent(instance) {
  instance.proxy = new Proxy(instance.ctx, componentPublicInstance as any);
  // setupÁöÑËøîÂõûÂÄºÔºåÂèòÊàêrenderÂáΩÊï∞ÁöÑÂèÇÊï∞this
  // Ëé∑ÂèñÁªÑ‰ª∂ÁöÑÁ±ªÂûã
  let component = instance.type;
  let { setup } = component;
  // Âà§Êñ≠ÁªÑ‰ª∂ÊúâÊ≤°Êúâsetup
  if (setup) {
    const setupContext = createContext(instance);
    let setupResult = setup(instance.props, setupContext);
    // ËøîÂõûÂÄºÂØπË±°ÔºåÂáΩÊï∞
    handleSetupResult(instance, setupResult); // Â¶ÇÊûúÊòØÂØπË±°ÔºåÂ∞ÜÂÄºÊîæÂÖ•ÂÆû‰æã‰∏äÁöÑsetupStateÔºåÂ¶ÇÊûúÂáΩÊï∞Â∞±ÊòØrender
  } else {
    // setupÊ≤°ÊúâÔºåË∞ÉÁî®render
    finishComponentSetup(instance);
  }

  // component.render(instance.proxy);
}
// Â§ÑÁêÜsetupÁöÑËøîÂõûÁªìÊûú
function handleSetupResult(instance, setupResult) {
  if (isFunction(setupResult)) {
    instance.render = setupResult; // Â∞ÜsetupËøîÂõûÁöÑÂáΩÊï∞‰øùÂ≠òÂà∞ÂÆû‰æã‰∏ä
    // ÂáΩÊï∞
  } else if (isObject(setupResult)) {
    instance.setupState = setupResult;
  }
  // ÊúÄÁªàÂéªÊâßË°årender
  finishComponentSetup(instance);
}
// Â§ÑÁêÜrender
function finishComponentSetup(instance) {
  let component = instance.type;
  // Âà§Êñ≠setupÊòØÂê¶Êúârender
  if(!instance.render) {
    // Ê≤°Êúârender
    // ËøõË°åÊ®°ÁâàÁºñËØë
    if(!component.render && component.template) {
      // Ê®°ÁâàÂèò ==>render
    }
    // Â∞ÜÂÆû‰æã‰∏äÁöÑsetupÊîæÂÖ•renderÈáå
    instance.render = component.render;
  }
}
// ÂàõÂª∫‰∏ä‰∏ãÊñá ctx
function createContext(instance) {
  return {
    attrs: instance.attrs,
    slot: instance.slots,
    emit: () => {},
    expose: () => {}
  };
}