import { isFunction } from '@vue/shared';
import { effect } from '.';

// ËÆ°ÁÆóÂ±ûÊÄß
export function computed(getterOrOptions) {
  // 1.ÂáΩÊï∞ 2.ÂØπË±°
  // Ëé∑ÂèñÊï∞ÊçÆ
  let getter; // Ëé∑Âèñ
  let setter; // ËÆæÁΩÆ
  if (isFunction(getterOrOptions)) {
    getter = getterOrOptions;
    setter = () => {
      console.warn('computed value must be readonly');
    };
  } else {
    // {get(),set()}
    // ÂØπË±°
    getter = getterOrOptions.get;
    setter = getterOrOptions.set;
  }
  return new ComputedRefImpl(getter, setter);
}

class ComputedRefImpl {
  public _dirty = true; // ÈªòËÆ§Ëé∑ÂèñÊâßË°å
  public _value;
  public effect;
  constructor(getter, public setter) {
    // ÂΩìÂàõÂª∫‰∏Ä‰∏™computedÁöÑÊó∂ÂÄôÔºåÂ∞±ÂΩìÂâçÁöÑgetter‰Ωú‰∏∫effectÁöÑÂõûË∞ÉÂáΩÊï∞
    // Âπ∂‰∏îÂ∞ÜeffectÂ≠òÂà∞Ëá™Â∑±ÁöÑeffect
    this.effect = effect(getter, {
      lazy: true,
      scheduler: () => {
        // ÂΩì‰øÆÊîπÊï∞ÊçÆÁöÑÊó∂ÂÄôÊâßË°å
        if (!this._dirty) {
          this._dirty = true;
        }
      }
    });
  }
  // ÈÄöËøá.value
  get value() {
    // ÂΩìËé∑Âèñxxx.valueÁöÑÊó∂ÂÄôÔºåÊâßË°åeffectÂáΩÊï∞ÔºåÂêßeffectÊâßË°åÂêéÁöÑÊï∞ÊçÆÊîæÂú®_value,Âπ∂ËøîÂõûËøô‰∏™Êï∞ÊçÆ
    // Â¶ÇÊûúÂΩìÂâçdirty‰∏∫falseÔºå‰ª£Ë°®ÂΩìÂâç‰æùËµñÂìçÂ∫îÊú™ÂèëÁîüÂèòÂåñÔºå‰ªéÁºìÂ≠òÊãøÂÄº
    if (this._dirty) {
      console.log('ËøõÂÖ•ÁºìÂ≠òÂà§Êñ≠ÔºåÂΩìÂâçdirty‰∏∫trueÔºåÊâßË°åeffect');
      this._dirty = false; // ÂèòÊõ¥‰∏∫false
      this._value = this.effect(); // Ëé∑ÂèñÁî®Êà∑ÁöÑÂÄºÔºåÂè™Ë¶ÅÊï∞ÊçÆË¢´ÊîπÂèò
    }
    return this._value;
  }
  set value(newValue) {
    this.setter(newValue);
  }
}
