import { hasChanged, hasOwn, isArray, isIntegerKey, isObject } from '@vue/shared';
import { Track, trigger } from './effect';
import { TrackOpTypes, TriggerOpTypes } from './operations';
import { readonly, reative } from './reative';
// Â§ÑÁêÜget
function createGetter(isReadonly = false, shallow = false) {
  return function get(target, key, receiver) {
    const res = Reflect.get(target, key, receiver); // target[key]
    // ‰∏çÊòØÂè™ËØª
    if (!isReadonly) {
      // Êî∂ÈõÜ‰æùËµñeffect,ÁõÆÊ†áÂØπË±°ÁöÑkeyÈááÁî®ÁöÑÊìç‰Ωúget
      Track(target, TrackOpTypes.GET, key);
    }
    // ÊµÖ
    if (shallow) {
      return res;
    }
    // keyÊòØ‰∏Ä‰∏™ÂØπË±°,ÈÄíÂΩíÔºåÊáí‰ª£ÁêÜÔºåÂç≥ÂΩìËÆøÈóÆ‰∏ã‰∏ÄÂ±ÇÂØπË±°ÊâçÂºÄÂßãËøõË°åÊã¶Êà™ÔºåÂøÖÈ°ªË¢´‰ΩøÁî®
    if (isObject(res)) {
      return isReadonly ? readonly(res) : reative(res);
    }
    return res;
  };
}

// ÊüØÈáåÂåñÔºåÊ†πÊçÆ‰∏çÂêåÂèÇÊï∞Â§ÑÁêÜÈÄªËæë
// Â§ÑÁêÜget
const get = /*#__PURE__*/ createGetter(); // Ê∑±Â∫¶ÈùûÂè™ËØª
const shallowGet = /*#__PURE__*/ createGetter(false, true); // ÈùûÂè™ËØª ÊµÖ
const readonlyGet = /*#__PURE__*/ createGetter(true); // Âè™ËØª Ê∑±Â∫¶
const shallowReadonlyGet = /*#__PURE__*/ createGetter(true, true); // Âè™ËØª ÊµÖ

// Â§ÑÁêÜset,ÊòØÂê¶ÊµÖ
function createSetter(shallow = false) {
  return function set(target, key, value, receiver) {
    // Â≠òÂÇ®ÊóßÂÄº
    const oldValue = target[key];
    // ËÆæÁΩÆÁöÑÊòØÊï∞ÁªÑËøòÊòØÂØπË±°ÔºåÊ∑ªÂä†ÂÄºËøòÊòØ‰øÆÊîπ
    // Âà§Êñ≠ÊòØÂê¶ÊòØÊï∞ÁªÑÔºåproxyÁöÑkeyÂ∞±ÊòØÊï∞ÁªÑÁöÑÁ¥¢ÂºïÔºåÂ¶ÇÊûúkeyÂ§ß‰∫élengthË°®Á§∫Êñ∞Â¢ûfalse,Â∞è‰∫éË°®Á§∫‰øÆÊîπtrue
    // Â¶ÇÊûúÊòØÂØπË±°ÔºåÂ¶ÇÊûúÂ≠òÂú®Â±ûÊÄßÂ∞±ÊòØ‰øÆÊîπtrueÔºå‰∏çÂ≠òÂú®Â∞±ÊòØÊñ∞Â¢ûÂ±ûÊÄßflase
    let hadKey = isArray(target) && isIntegerKey(key) ? Number(key) < target.length : hasOwn(target, key);

    const result = Reflect.set(target, key, value, receiver);
    if (!hadKey) {
      // Ê≤°ÊúâÔºåËß¶ÂèëÊñ∞Â¢ûÊï∞ÊçÆ
      console.log('ÂΩìÂâçÂÄº‰∏∫Êñ∞Â¢ûÔºåËß¶ÂèësetÊñ∞Â¢û==>', key);
      // Êñ∞Â¢û keyÊìç‰ΩúÁöÑÂ±ûÊÄß value Êñ∞ÂÄº
      trigger(target, TriggerOpTypes.ADD, key, value);
    } else {
      // ÊúâÔºå‰øÆÊîπÂÄº
      // Â¶ÇÊûúÊñ∞ÂÄºÂíåÊóßÂÄº‰∏çÁõ∏Âêå
      if (hasChanged(value, oldValue)) {
        console.log('ÂΩìÂâçÂÄº‰∏∫‰øÆÊîπÔºåËß¶Âèëset‰øÆÊîπ');
        trigger(target, TriggerOpTypes.SET, key, value, oldValue);
      }
    }
    return result;
  };
}
const set = /*#__PURE__*/ createSetter();
const shallowSet = /*#__PURE__*/ createSetter(true);
// ÂÖ®ÈÉ®‰ª£ÁêÜ
export const reativeHandlers = {
  get,
  set
};
// Êï∞ÊçÆÁ¨¨‰∏ÄÂ±Ç‰ª£ÁêÜ
export const shallowReactiveHandlers = {
  get: shallowGet,
  set: shallowSet
};
// ÂÖ®ÈÉ®Âè™ËØª
export const readonlyHandlers = {
  get: readonlyGet,
  set: (target, key, value) => {
    console.error('‰∏çÂèØ‰ª•‰øÆÊîπkeyÔºåÂè™ËØªÂÖ®ÈÉ®');
  }
};
// Á¨¨‰∏ÄÂ±ÇÂè™ËØª
export const shallowReativeHandlers = {
  get: shallowReadonlyGet,
  set: (target, key, value) => {
    console.error('‰∏çÂèØ‰ª•‰øÆÊîπkeyÂè™ËØª‰∏ÄÂ±Ç');
  }
};
