import { isArray, isIntegerKey } from '@vue/shared';
import { TriggerOpTypes } from './operations';

export function effect(fn, options: any = {}) {
  const effect = createReactiveEffect(fn, options);
  // Âà§Êñ≠options
  if (!options.lazy) {
    console.log('Áî®Êà∑Ê≤°Êúâ‰º†ÈÄílazyÔºåÁ´ãÂç≥ÊâßË°å‰∏ÄÊ¨°');
    effect(); // ÈªòËÆ§ÊâßË°å
  }
  return effect;
}
let uid = 0; // ËÆ∞ÂΩïeffectÁöÑ‰∏ãÊ†á
let activeEffect; // ‰øùÂ≠òÂΩìÂâçÁöÑeffect
const effectTrack = []; // ÂÆö‰πâ‰∏Ä‰∏™Ê†àËß£ÂÜ≥ÂµåÂ•óÊ†ëÂûãÁªìÊûÑeffectÔºå‰∏∫‰∫ÜËß£ÂÜ≥ÂΩìÂâçeffectÊòØÂì™‰∏Ä‰∏™
/*
[effect1,effect2,effect3]]
effect(() => {
  state.name
  effect(() => {
    state.age
    effect(() => {
      state.a
    })
  })

})
*/
function createReactiveEffect(fn, options) {
  const effect = function reativeEffect() {
    if (!effectTrack.includes(effect)) {
      // ÂΩìÂâçÂàõÂª∫ÁöÑeffect‰∏çÂ≠òÂú®Ê†à‰∏≠,ÂàõÂª∫effect
      // Ê≤°ÊúâÂÖ•Ê†àÂΩìÂâçÁöÑeffect
      try {
        // ÂÖ•Ê†à
        effectTrack.push(effect);
        activeEffect = effect;
        // ÂìçÂ∫îÂºèeffect
        return fn(); // ÊâßË°åÁî®Êà∑ÁöÑÊñπÊ≥ï
      } finally {
        // Êó†ËÆ∫ÊòØÂê¶ÊàêÂäüÔºåÈÉΩÊâßË°å
        // Âá∫Ê†à
        effectTrack.pop();
        activeEffect = effectTrack[effectTrack.length - 1];
      }
    }
  };
  effect.id = uid++; // Âå∫Âà´effect
  effect._isEffect = true; // Âå∫ÂàÜÊòØÂê¶ÂìçÂ∫î
  effect.raw = fn; // ‰øùÂ≠òÁî®Êà∑ÊñπÊ≥ï
  effect.options = options; // ‰øùÂ≠òÁî®Êà∑ÈÖçÁΩÆ
  return effect;
}

// Êî∂ÈõÜeffectÔºåËé∑ÂèñÊï∞ÊçÆÁöÑÊó∂ÂÄôËß¶Âèëget Êî∂ÈõÜeffect
let targetMap = new WeakMap(); // ÂàõÂª∫Ë°®
export function Track(target, type, key) {
  console.log('Ëß¶ÂèëÊî∂ÈõÜ', 'Ë¢´Êî∂ÈõÜÁöÑtarget==>', target, 'Ë¢´Êî∂ÈõÜÁöÑkey==>', key);
  // 1.name ==> effect
  // keyÂíåeffect‰∏Ä‰∏ÄÂØπÂ∫î
  if (typeof activeEffect === 'undefined') {
    // ÂΩìÂâçÂØπË±°Ê≤°ÊúâÁöÑÂú®effectÂáΩÊï∞‰∏≠‰ΩøÁî®
    return;
  }
  // key===target==Â±ûÊÄß==>Set[effect,effect]
  let depMap = targetMap.get(target); //
  if (!depMap) {
    targetMap.set(target, (depMap = new Map())); // Ê∑ªÂä†ÂÄº
  }
  // ÂΩìÂâçkey
  let dep = depMap.get(key);
  // Âà§Êñ≠keyÊòØÂê¶Â≠òÂú®Ôºå‰∏çÂ≠òÂú®Â∞±ÂàõÂª∫‰∏Ä‰∏™set
  if (!dep) {
    depMap.set(key, (dep = new Set()));
  }
  // Â¶ÇÊûúÂΩìÂâçset‰∏çÂ≠òÂú®ÂΩìÂâçeffectÔºå
  if (!dep.has(activeEffect)) {
    // Êî∂ÈõÜeffectÔºåÂπ∂Ê∑ªÂä†ËøõÂéª
    dep.add(activeEffect);
  }
  console.log('‰æùËµñÊî∂ÈõÜÂÆåÊàêÔºåÂΩìÂâçÂàõÂª∫ÁöÑ‰æùËµñweakmap==>', targetMap);
}

// Ëß¶Âèë‰æùËµñ
// 1.Â§ÑÁêÜÂØπË±°
export function trigger(target, type, key?, newValue?, oldValue?) {
  // Ëß¶ÂèëÊõ¥Êñ∞
  const depsMap = targetMap.get(target);
  // Âà§Êñ≠ÁõÆÊ†áÂØπË±°ÊúâÊ≤°ÊúâË¢´Êî∂ÈõÜ==> ‰∏çÊòØÂìçÂ∫îÁöÑ
  if (!depsMap) return;
  const effectSet = new Set();
  // ÊÄßËÉΩ‰ºòÂåñÂ¶ÇÊûúÊúâÂ§ö‰∏™ÂêåÊó∂‰øÆÊîπ‰∏Ä‰∏™ÂÄºÔºåÁõ∏ÂêåÂ∞±ËøáÊª§
  const add = (effectAdd) => {
    if (effectAdd) {
      effectAdd.forEach((effect) => {
        effectSet.add(effect);
      });
    }
  };
  // ‰øÆÊîπÊï∞ÁªÑÈïøÂ∫¶ÔºåÊï∞ÁªÑÁâπÊÆäÂ§ÑÁêÜ
  if (key === 'length' && isArray(target)) {
    // proxyÂú®Ê∑ªÂä†Êï∞ÁªÑÁöÑÊó∂ÂÄôÈªòËÆ§‰ºöËÆ≤lengthÁ≠âÂ±ûÊÄßÂä†ÂÖ•
    // Âú®effect‰∏≠length‰πüË¢´Ê∑ªÂä†‰æùËµñ
    // ÂΩìÁî®Êà∑‰øÆÊîπÈïøÂ∫¶ÔºåÊàñËÄÖ‰øÆÊîπÁöÑ‰∏ãÊ†áÂ∞è‰∫éÊâÄÊúâÊï∞ÁªÑÁöÑ‰∏ãÊ†á
    // Â∞ÜlengthÂíå‰∏ãÊ†áÁöÑeffectÊîæÂÖ•dep‰∏≠
    depsMap.forEach((dep, key) => {
      if (key === 'length' || key >= newValue) {
        add(dep);
      }
    });
  } else {
    // ÂèØËÉΩÊòØÂØπË±°
    if (key !== undefined) {
      // Ëé∑ÂèñÂΩìÂâçkey‰∏ãÁöÑeffect
      add(depsMap.get(key));
    }
    switch (type) {
      // Â¶ÇÊûúÊòØÊñ∞Â¢ûÂ±ûÊÄß
      case TriggerOpTypes.ADD:
        if (isArray(target) && isIntegerKey(key)) {
          // Â¶ÇÊûúÊòØ‰∏™Êï∞ÁªÑ‰∏îkey‰∏∫Êï¥Êï∞ÔºåÂ∞±Â∞ÜlengthÁöÑeffectÂä†ÂÖ•‰æùËµñ
          add(depsMap.get('length'));
        }
    }
  }
  // Ëß¶ÂèëeffectÊñπÊ≥ï
  effectSet.forEach((effect: any) => {
    if (effect.options.scheduler) {
      // ÂΩìÂâçschedulerÂ≠òÂú®Ôºåeffect‰∏∫computed,effectÁöÑÂâØ‰ΩúÁî®ÊîæÂú®getÈáå
      effect.options.scheduler();
    } else {
      effect();
    }
  });
  // ÊâßË°å
}
