import { extend } from '@vue/shared';
import { nodeOps } from './nodeOps';
import { patchProp } from './patchProp';
import { createRenderer } from '@vue/runtime-core';
export const rendererOptions = extend({ patchProp }, nodeOps);

export const creatApp = (rootcomponent, rootProps) => {
  // Âπ≥Âè∞Âà§Êñ≠ÔºåÂàõÂª∫‰∏Ä‰∏™Ê∏≤ÊüìÂô®
  const app = createRenderer(rendererOptions).createApp(rootcomponent, rootProps);
  const { mount } = app;
  app.mount = function (container) {
    // #app
    // ÊåÇËΩΩÁªÑ‰ª∂
    // 1.ÂÖàÊ∏ÖÁ©∫ÂÆπÂô®ÂÜÖÁöÑÂÜÖÂÆπ
    container = rendererOptions.querySelector(container);
    container.innerHTML = '';
    // Â∞ÜÁªÑ‰ª∂ÁöÑdomÂÖÉÁ¥†ËøõË°åÊåÇËΩΩ
    mount(container);
  };
  return app;
};

export * from '@vue/runtime-core';

export * from '@vue/reativity'