/**
 * @description: styleÁöÑÂ§ÑÁêÜ
 * @param {*} el ÁõÆÊ†áÂÖÉÁ¥†
 * @param {*} prev ‰πãÂâçÁöÑÂÄº
 * @param {*} next ‰øÆÊîπÁöÑÂÄº
 */
export const patchStyle = (el: Element, prev, next) => {
  const style = (el as HTMLElement).style;
  if (next == null) {
    // Âà†Èô§
    el.removeAttribute('style');
  } else {
    // ËÄÅÁöÑÊúâÔºåÊñ∞ÁöÑÊ≤°Êúâ
    if (prev) {
      for (let key in prev) {
        if (next[key] == null) {
          style[key] = '';
        }
      }
    }
    // ËÄÅÁöÑÊ≤°ÊúâÔºåÊñ∞ÁöÑÊúâ
    for (let key in next) {
      style[key] = next[key];
    }
  }
};
