import { effect } from '@vue/reativity';
import { ShapeFlags } from '@vue/shared';
import { ApiCreateApp } from './apiCreateApp';
import { createComponentInstance, setupComponent } from './component';
import { CVnode, TEXT } from './vnode';
/**
 * @description: Âπ≥Âè∞Âà§Êñ≠ÔºåÂàõÂª∫Ê∏≤ÊüìÂô®
 * @return {Function} createApp ÊåÇËΩΩÂáΩÊï∞
 */
export function createRenderer(rendererOptions) {
  // Ëé∑ÂèñÂÖ®ÈÉ®ÁöÑdomÊìç‰Ωú

  const {
    insert: hostInsert,
    remove: hostRemove,
    patchProp: hostPatchProp,
    createElement: hostCreateElement,
    createText: hostCreateText,
    setText: hostSetText,
    setElementText: hostSetElementText
  } = rendererOptions;
  // ÁªôsetupÂàõÂª∫effect
  function setupRenderEffect(instance, container) {
    // ÊØèÊ¨°Êï∞ÊçÆÂèòÂåñÈáçÊñ∞ÊâßË°årender
    effect(function componentEffect() {
      console.log('effect');
      // Âà§Êñ≠ÊòØ‰∏çÊòØÁ¨¨‰∏ÄÊ¨°Âä†ËΩΩ
      if (!instance.isMounted) {
        // Ëé∑ÂèñrenderÁöÑËøîÂõûÂÄº
        const proxy = instance.proxy;
        // ÊâßË°årenderÊñπÊ≥ïËé∑Âèñvnode
        let subTree = (instance.subTree = instance.render.call(proxy, proxy));
        console.log('subTree', subTree);
        // Ê∏≤ÊüìÂ≠êÊ†ë ÂàõÂª∫ÂÖÉÁ¥†
        patch(null, subTree, container);
        instance.isMounted = true;
      } else {
        // Êõ¥Êñ∞
        console.log('Êõ¥Êñ∞');
        const proxy = instance.proxy;
        // ÂØπÂêßÊóßÁöÑÂÄºÂíåÊñ∞ÁöÑÂÄº
        const prevTree = instance.subTree;
        const nextTree = instance.render.call(proxy, proxy);
        instance.subTree = nextTree;
        patch(prevTree, nextTree, container);
      }
    });
  }
  // ------------------Â§ÑÁêÜÁªÑ‰ª∂
  const mountComponent = (initialVNode, container) => {
    // ÁªÑ‰ª∂ÂàùÂßãÂåñÊµÅÁ®ã
    // 1.ÂÖàÊúâ‰∏Ä‰∏™ÁªÑ‰ª∂ÁöÑÂØπË±°renderÔºàproxyÔºâ
    const instance = (initialVNode.component = createComponentInstance(initialVNode));
    // 2.Ëß£ÊûêÊï∞ÊçÆÂà∞ÂÆû‰æãÂØπË±°
    setupComponent(instance);
    // 3.ÂàõÂª∫effectËÆ©renderÊâßË°å
    setupRenderEffect(instance, container);
  };
  // ÁªÑ‰ª∂ÂàõÂª∫
  const processComponent = (n1, n2, container) => {
    if (n1 == null) {
      // Á¨¨‰∏ÄÊ¨°ÊàñËÄÖÈáçÊñ∞Âä†ËΩΩ
      mountComponent(n2, container);
    } else {
      // Âêå‰∏Ä‰∏™ÂÖÉÁ¥†
      console.log('Âêå‰∏Ä‰∏™ÁªÑ‰ª∂ÊØîÂØπ');
    }
  };
  // ----------------------Â§ÑÁêÜÊñáÊú¨
  function processText(n1, n2, container) {
    if (n1 == null) {
      hostInsert(hostCreateText(n2.children), container);
    } else {
      // Êõ¥Êñ∞
    }
  }
  //  -------------------Â§ÑÁêÜÂÖÉÁ¥†
  // Âä†ËΩΩÂÖÉÁ¥†
  function mountChildren(el, children) {
    for (let i = 0; i < children.length; i++) {
      let child = CVnode(children[i]);
      // ÂàõÂª∫ÊñáÊú¨ÂÖÉÁ¥†
      patch(null, child, el);
    }
  }
  function mountedElement(vnode, container) {
    // ÂàõÂª∫ÂÖÉÁ¥†
    // h('div',{},[h('div')])
    // ÈÄíÂΩíÊ∏≤ÊüìÂèòÊàêÁúüÂÆûdom
    const { props, shapeFlag, type, children } = vnode;
    // ÂàõÂª∫ÂÖÉÁ¥†
    let el = hostCreateElement(type);
    vnode.el = el; // ‰øùÂ≠òÂΩìÂâçÁúüÂÆûdom
    // Ê∑ªÂä†Â±ûÊÄß
    if (props) {
      for (let key in props) {
        hostPatchProp(el, key, null, props[key]);
      }
    }
    // Â§ÑÁêÜchildren// h('div',{style:{color:'red'}},'text')
    // Â§ÑÁêÜchildren// h('div',{style:{color:'red'}},['text'])
    // Â§ÑÁêÜchildren// h('div',{style:{color:'red'}},[h()])
    if (children) {
      if (shapeFlag & ShapeFlags.TEXT_CHILDREN) {
        // ÊñáÊú¨ÔºåÂàõÂª∫ÊñáÊú¨ÂÖÉÁ¥†
        hostSetElementText(el, children);
      } else if (shapeFlag & ShapeFlags.ARRAY_CHILDREN) {
        // ÈÄíÂΩí patch Ë¶ÅÊ±ÇÔºöÊòØ‰∏Ä‰∏™ËôöÊãüdom
        mountChildren(el, children);
      }
    }
    // ÊîæÂà∞ÂØπÂ∫îÁöÑ‰ΩçÁΩÆ
    hostInsert(el, container);
  }
  // Â±ûÊÄßÊØîÂØπ
  const patchProps = (el, oldProps, newProps) => {
    // ÊóßÁöÑÊúâÂ±ûÊÄßÔºåÊñ∞ÁöÑÊ≤°ÊúâËøô‰∏™Â±ûÊÄß
    // Âæ™ÁéØÊñ∞ÁöÑ
    if (oldProps != newProps) {
      for (let key in newProps) {
        const prev = oldProps[key]; // ÊóßÂ±ûÊÄß
        const next = newProps[key]; // ÊóßÂ±ûÊÄß
        if (prev != next) {
          // ‰∏çÁõ∏ÂêåÁöÑÂ∞±ÊâßË°åÊõøÊç¢
          console.log('ÂΩìÂâçÊõøÊç¢ÁöÑÂ±ûÊÄß==>', key, 'Ë¢´ÊõøÊç¢ÁöÑÂÄº', prev, '==>', next);
          hostPatchProp(el, key, prev, next);
        }
      }
    }
    // Âæ™ÁéØÊóßÁöÑÔºåÂ¶ÇÊûúÊóßÁöÑÂ±ûÊÄß‰∏çÂú®Êñ∞ÁöÑÂ±ûÊÄßÈáåÔºåÁõ¥Êé•Âà†Èô§
    for (let key in oldProps) {
      if (!(key in newProps)) {
        console.log('Ë¢´Âà†Èô§ÁöÑÂ±ûÊÄßkey==>', key, 'Ë¢´Âà†Èô§ÁöÑÂ±ûÊÄßÂÄºvalue', oldProps[key]);
        hostPatchProp(el, key, oldProps[key], null);
      }
    }
  };
  // Âêå‰∏Ä‰∏™ÂÖÉÁ¥†ÊØîÂØπ
  const patchElement = (n1, n2, container) => {
    // Â±ûÊÄßÊØîÂØπstyle class
    let el = (n2.el = n1.el); // Ëé∑ÂèñÁúüÂÆûÁöÑËäÇÁÇπ
    const oldProps = n1.props || {};
    const newProps = n2.props || {};
    patchProps(el, oldProps, newProps); // Â§ÑÁêÜÂ±ûÊÄß
    // ÊØîÂØπchildren
    patchChild(n1, n2, el);
  };
  // ÊØîÂØπchildren
  const patchChild = (n1, n2, el) => {
    console.log('patchChild==>>>');
    const c1 = n1.children;
    const c2 = n2.children;
    // 1.ÊóßÁöÑÊúâÊñ∞ÁöÑÊ≤°Êúâ
    // 2.Êñ∞ÁöÑÊúâ„ÄÇÊóßÁöÑÊ≤°Êúâ
    // 3.Êñ∞ÊóßÈÉΩÊòØÊñáÊú¨
    // 4.ÈÉΩÊúâÂÑøÂ≠êÔºåÂπ∂‰∏îËøô‰∫õÂÑøÂ≠êÈÉΩÊòØÊï∞ÁªÑ
    const prevShapeFlag = n1.shapeFlag;
    const newShapeFlag = n2.shapeFlag;
    if (newShapeFlag & ShapeFlags.TEXT_CHILDREN) {
      // ÊñáÊú¨Á±ªÂûã
      hostSetElementText(el, c2);
    } else {
      // ‰∏çÊòØÊñáÊú¨Â∞±ÊòØÊï∞ÁªÑ
      if (prevShapeFlag & ShapeFlags.ARRAY_CHILDREN) {
        // ÊóßÁöÑ‰πüÊòØÊï∞ÁªÑ
        patchKeyChild(c1, c2, el);
      } else {
        // ÊóßÁöÑÊòØ‰∏Ä‰∏™ÊñáÊú¨ÔºåËÆ≤ÊóßÁöÑÂà†Èô§
        hostSetElementText(el, '');
        // Ê∑ªÂä†ÂÑøÂ≠ê
        mountChildren(el, c2);
      }
    }
  };
  // ÂÑøÂ≠êÈÉΩÊòØÊï∞ÁªÑÁöÑÊÉÖÂÜµÊØîÂØπ
  const patchKeyChild = (c1, c2, el) => {
    // vue2 ÂèåÊåáÈíàÊñπÂºè
    // vue3 ÂêåÊ≠•
    // sync from start Ôºö‰ªéÂ§¥ÈÉ®ÊØîÂØπ
    let i = 0; // ÊØîÂØπÁöÑ‰ΩçÁΩÆ
    let e1 = c1.length - 1;
    let e2 = c2.length - 1;
    // üôÜÂêå‰∏Ä‰ΩçÁΩÆ‰∏çÂêåÔºåÂÅúÊ≠¢Ôºå‰∏§‰∏™Êï∞ÁªÑÂÖ∂‰∏≠‰∏Ä‰∏™Ê≤°Êúâ ÂÅúÊ≠¢
    while (i <= e1 && i <= e2) {
      const n1 = c1[i];
      const n2 = c2[i];
      if (isSameVnode(n1, n2)) {
        // ÈÄíÂΩíÂà§Êñ≠Â≠êÂÖÉÁ¥†
        patch(n1, n2, el);
      } else {
        break; // ÂÅúÊ≠¢
      }
      i++;
    }
    // sync from end Ôºö‰ªéÂ∞æÈÉ®ÊØîÂØπ
    // while (i <= e1 && i <= e2) {
    //   const n1 = c1[i];
    //   const n2 = c2[i];
    //   if (isSameVnode(n1, n2)) {
    //     // ÈÄíÂΩíÂà§Êñ≠Â≠êÂÖÉÁ¥†
    //     patch(n1, n2, el);
    //   } else {
    //     break; // ÂÅúÊ≠¢
    //   }
    //   e1--;
    //   e2--;
    // }
  };
  function processeElement(n1, n2, container) {
    if (n1 == null) {
      // ÂàõÂª∫
      mountedElement(n2, container);
    } else {
      // Êõ¥Êñ∞
      console.log('Âêå‰∏Ä‰∏™ÂÖÉÁ¥†ÊØîÂØπ');
      patchElement(n1, n2, container);
    }
  }
  // Âà§Êñ≠ÊòØ‰∏çÊòØÂêå‰∏Ä‰∏™ÂÖÉÁ¥†
  function isSameVnode(n1, n2) {
    return n1.type == n2.type && n1.key == n2.key;
  }
  // Âà†Èô§
  function unmount(vnode) {
    console.log(vnode.el);
    hostRemove(vnode.el);
  }
  //
  /**
   * @description: Ê∏≤Êüì
   * @param {*} n1 ‰∏ä‰∏Ä‰∏™ËäÇÁÇπ
   * @param {*} n2 ÈúÄË¶ÅÊ∏≤ÊüìÁöÑËäÇÁÇπ
   * @param {*} container Ê∏≤ÊüìÁöÑÁõÆÊ†áÂÆπÂô®
   * @return {*}
   */
  function patch(n1, n2, container) {
    // Âà§Êñ≠ÊòØ‰∏çÊòØÂêå‰∏Ä‰∏™ÂÖÉÁ¥†
    if (n1 && !isSameVnode(n1, n2)) {
      // ‰∏çÊòØÂêå‰∏Ä‰∏™ÂÖÉÁ¥†ÔºåÁõ¥Êé•ÊõøÊç¢
      unmount(n1); // Âà†Èô§‰πãÂâçÁöÑÂÖÉÁ¥†
      n1 = null;
    }
    const { shapeFlag, type } = n2;
    // ÊñáÊú¨
    switch (type) {
      case TEXT:
        // ÊñáÊú¨Á±ªÂûã
        processText(n1, n2, container);
        break;

      default:
        break;
    }
    if (shapeFlag & ShapeFlags.ELEMENT) {
      console.log('ÂΩìÂâçÊ∏≤ÊüìÁöÑÊòØÂÖÉÁ¥†');
      processeElement(n1, n2, container);
    } else if (shapeFlag & ShapeFlags.STATEFUL_COMPONENT) {
      console.log('ÂΩìÂâçÊ∏≤ÊüìÁöÑÊòØÁªÑ‰ª∂');
      processComponent(n1, n2, container);
    }
  }
  /**
   * @description: ÂÆûÁé∞Ê∏≤ÊüìÔºåÁªÑ‰ª∂ÂàùÂßãÂåñ
   * @param {*} vnode ËôöÊãüdom
   * @param {*} container ÂÆπÂô®
   * @return {*} container
   */
  function render(vnode, container) {
    // ÁªÑ‰ª∂ÂàùÂßãÂåñ
    // Ê∏≤Êüì
    patch(null, vnode, container);
  }
  return {
    createApp: ApiCreateApp(render) // ÂàõÂª∫vnode
  };
}

// ÁªÑ‰ª∂ÂàùÂßãÂåñÊµÅÁ®ãÔºö Â∞ÜÁªÑ‰ª∂ÂèòÊàêvnode ==> ÂàõÂª∫‰∏Ä‰∏™ÁªÑ‰ª∂ÂÆû‰æã ==>ÂÜçËøõË°åÊ∏≤Êüìvnode ==> ÁúüÂÆûdom
