import { hasChanged, isArray } from '@vue/shared';
import { Track, trigger } from './effect';
import { TrackOpTypes, TriggerOpTypes } from './operations';

export function ref(target) {
  return createRef(target);
}
export function shallowRef(target) {
  // ÊµÖÔºåÂ¶ÇÊûúÊòØ‰∏Ä‰∏™ÂØπË±°
  return createRef(target, true);
}
// ÂàõÂª∫Á±ªRefImpl
class RefImpl {
  public __v_isRef = true; // Ê†áËØÜref‰ª£ÁêÜ
  public _value;
  constructor(public rawValue, public shallow) {
    this._value = rawValue; // ÂéüÊù•ÁöÑÂÄº
  }
  // Á±ªÁöÑÂ±ûÊÄßËÆøÈóÆÂô®,Êî∂ÈõÜ‰æùËµñtrack
  get value() {
    Track(this, TrackOpTypes.GET, 'value');
    return this._value;
  }
  set value(newValue) {
    // ‰øÆÊîπÔºåËß¶Âèë‰æùËµñ
    if (hasChanged(newValue, this._value)) {
      this._value = newValue;
      this.rawValue = newValue;
      console.log('Ëß¶Âèëset--value');
      trigger(this, TriggerOpTypes.SET, 'value', newValue);
    }
  }
}
/**
 * @description: ÂàõÂª∫refÔºåËøîÂõûÂÆû‰æãÂØπË±°
 * @param {*} rawValue
 * @param {Boolean} shallow
 * @return {Object} RefImpl
 */
function createRef(rawValue, shallow = false) {
  // ÂàõÂª∫refÔºåËøîÂõûÂÆû‰æãÂØπË±°
  return new RefImpl(rawValue, shallow);
}
/**
 * @description: ÁõÆÊ†áÂØπË±°Â¶ÇÊûúÊòØÂìçÂ∫îÂºèÔºåËøîÂõûÁöÑÂ±ûÊÄßÊòØÂìçÂ∫îÁöÑÔºåÂ¶ÇÊûúÊòØÊôÆÈÄöÁöÑÂ∞±ÊòØÊôÆÈÄöÁöÑ
 * @param {Object} target
 * @param {String} key
 * @return {ObjectRefImpl} ObjectRefImpl
 */
export function toRef(target, key) {
  return new ObjectRefImpl(target, key);
}
/**
 * @description: torefËøîÂõûÁöÑÂÄºÔºåÊú¨Ë¥®ÊòØÈÄöËøáÊ®°Êãüvalue Ëß¶ÂèëreativeÁöÑgetÂíåset
 * @param {Object} target
 * @param {String} key
 */
class ObjectRefImpl {
  public __v_isRef = true; // Ê†áËØÜref‰ª£ÁêÜ
  constructor(public target, public key) {}
  get value() {
    return this.target[this.key];
  }
  set value(newValue) {
    this.target[this.key] = newValue;
  }
}
/**
 * @description: reativeÁöÑÊâÄÊúâÂÄºÂèòÊàêObjectRefImplÂÆû‰æã
 * @param {object} reative
 * @return {object} Record<string,ObjectRefImpl>
 */
export function toRefs(target) {
  // ÈÅçÂéÜÂΩìÂâçÂØπË±°
  // Â¶ÇÊûúÊòØÊï∞ÁªÑ new Array
  let ret = isArray(target) ? new Array(target.length) : {};
  for (let key in target) {
    // Â∞ÜÂØπË±°ÂèòÊàêtoref
    ret[key] = toRef(target, key);
  }
  return ret;
}
