import { isArray, isObject } from '@vue/shared';
import { creatVnode, isVnode } from './vnode';

export function h(type, propsOrChildren, childen = null) {
  // ÂèòÊàêvnode

  // h('div',{},'hello')
  // h('div',{},[h('span')])
  const i = arguments.length; // ÂèÇÊï∞‰∏™Êï∞
  if (i === 2) {
    if (isObject(propsOrChildren) && !isArray(propsOrChildren)) {
      // h('div',{})
      // h('div',h('div'))
      // Âà§Êñ≠ÊòØ‰∏çÊòØ‰∏Ä‰∏™vnode
      if (isVnode(propsOrChildren)) {
        return creatVnode(type, null, [propsOrChildren]);
      }
      return creatVnode(type, propsOrChildren); // Ê≤°ÊúâÂÑøÂ≠êÔºåÂè™ÊúâÂ±ûÊÄß
    } else {
      return creatVnode(type, null, propsOrChildren); // ÊòØÂÑøÂ≠ê
    }
  } else {
    if (i > 3) {
      // h('div',{},'1','2')
      // Ë∂ÖËøá‰∏â‰∏™ÁöÑ
      childen = Array.prototype.slice.call(arguments, 2); // ‰ªéÁ¨¨‰∫å‰∏™ÂºÄÂßãÂàáÂâ≤
    } else if (i === 3 && isVnode(childen)) {
      childen = [childen]; // h('div',{},h('div'))
    }
    return creatVnode(type, propsOrChildren, childen);
  }
}
